import { ProCard } from "@ant-design/pro-components";
import { Col, Row, Segmented, Select, SelectProps } from "antd";
import Title from "antd/es/typography/Title";
import { useState } from "react";

const SettingView = () => {
    const types = ['基础配置', 'JS错误', '请求监控', '静态资源监控', '静态资源错误监控', '自定义监控', '站点性能', '白屏监控', '长任务', '用户行为'];
    const [type, setType] = useState(types[0]);
    const handleChangeType = function (value: string | number) {
        setType(value as string);
    }

    const whiteList: SelectProps['options'] = [];

    for (let i = 10; i < 36; i++) {
        whiteList.push({
            label: i.toString(36) + i,
            value: i.toString(36) + i,
        });
    }

    const handleWhiteListChange = (value: string[]) => {
        console.log(`selected ${value}`);
    };

    return (<div>
        <ProCard
            title="项目设置"
            bordered
            headerBordered
        >
            <div>
                <Title level={5}>上报配置</Title>
                <Row style={{marginTop:'10px'}}>
                    <Col span={18} push={6}>
                        <Segmented options={types} defaultValue={type[0]} onChange={handleChangeType} />
                    </Col>
                    <Col span={6} pull={18}>
                        类型
                    </Col>
                </Row>
                {type === '基础配置' && <Row style={{marginTop:'10px'}}>
                    <Col span={18} push={6}>
                        <Select
                            mode="multiple"
                            allowClear
                            style={{ width: '100%' }}
                            placeholder="请选择白名单用户"
                            defaultValue={['a10', 'c12']}
                            onChange={handleWhiteListChange}
                            options={whiteList}
                        />
                    </Col>
                    <Col span={6} pull={18}>
                        白名单客户
                    </Col>
                </Row>}
            </div>
        </ProCard>
    </div>);
}

export default SettingView;